.suffixContainer {
    font-size: 16px;
    line-height: 20px;
    font-weight: 400;

    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    display: none;
    pointer-events: none;
}

input:focus + .suffixContainer,
.suffixVisible .suffixContainer {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
}

.hasSuffix input:focus::-webkit-input-placeholder {
    color: transparent;
    -webkit-transition: none;
    transition: none;
}

.hasSuffix input:focus::-moz-placeholder {
    color: transparent;
    -moz-transition: none;
    transition: none;
}

.hasSuffix input:focus:-ms-input-placeholder {
    color: transparent;
    -ms-transition: none;
    transition: none;
}

.hasSuffix input:focus::-ms-input-placeholder {
    color: transparent;
    -ms-transition: none;
    transition: none;
}

.hasSuffix input:focus::placeholder {
    color: transparent;
    -webkit-transition: none;
    transition: none;
}

.spacer {
    visibility: hidden;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    white-space: pre;
}

.suffix {
    white-space: pre;
}

.disabled {
    /* TODO:   --input-disabled-color */
    color: var(--color-dark-indigo-60-flat);
}
