:root {
    /* bg */
    --circular-progress-bar-bg-color: var(--color-light-bg-tertiary);

    /* positive */
    --circular-progress-bar-positive-color: var(--color-light-graphic-positive);

    /* negative */
    --circular-progress-bar-negative-color: var(--color-light-graphic-negative);
}

.component {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.svg {
    display: block;
    width: 100%;
}

.title,
.subtitle {
    overflow: hidden;
    word-break: break-word;
}

.subtitle {
    max-height: 40px;
}

.l {
    width: 144px;
    height: 144px
}

.l .label {
        max-width: 120px;
    }

.l .title {
        max-height: 40px;
    }

.m {
    width: 120px;
    height: 120px
}

.m .label {
        max-width: 96px;
    }

.m .title {
        max-height: 24px;
    }

.label {
    text-align: center;
    position: absolute;
}

.backgroundCircle,
.progressCircle {
    width: 100%;
    height: 100%;
    fill: transparent;
    stroke-width: 8;
}

.positive {
    stroke: var(--circular-progress-bar-positive-color);
}

.negative {
    stroke: var(--circular-progress-bar-negative-color);
}

.backgroundCircle {
    stroke: var(--circular-progress-bar-bg-color);
}
